.label, .percentage {
    display: block;
    font-weight: 500;
    font-size: 16px;
    line-height: 1.18;
    letter-spacing: 0.03em;
    color: blue;
    margin-top: 0;
    margin-bottom: 10px;

}
.statList {
    
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style: none;
    padding: 20px;
    background-color: rgb(255, 205, 224);
}
.conteiner {
    
    margin: 30px auto;
    padding: 32px 16px;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12),
        0px 1px 1px rgba(0, 0, 0, 0.14),
        0px 2px 1px rgba(0, 0, 0, 0.2);
    border-radius: 0px 0px 4px 4px;
    max-width: 480px;
    
    
}
.title {
    text-align: center;
    font-weight: 700;
    font-size: 26px;
    line-height: 1.18;
    letter-spacing: 0.03em;
    color: brown;
    margin-top: 0;
    margin-bottom: 10px;

}